<template>
  <div>
    <div id="echarts_box" style="width: 600px;height:400px;"></div>
  </div>
</template>
<script>
import echarts from "echarts"
export default {
  data() {
    return {
      data: {
        id: JSON.parse(window.sessionStorage.user).u_id
      }
    }
  },
  // DOM 渲染完成触发
  mounted() {
    // 1. 基于准备好的dom，初始化echarts实例
    var myChart = echarts.init(document.getElementById("echarts_box"))
    // 3. 使用刚指定的配置项和数据，显示图表
    myChart.setOption(this.data)
  },
  created() {
    this.getstatistics()
  },
  methods: {
    async getstatistics() {
      const { data: res } = await this.$http.get("goods/showSaleChart", { params: { id: this.id } })
      console.log(res)
      if (res.code !== 200) return
    }
  }
}
</script>
